:root {
  --Scissors-Gradient: hsl(39, 89%, 49%) to hsl(40, 84%, 53%);
  --Paper-Gradient: hsl(230, 89%, 62%) to hsl(230, 89%, 65%);
  --Rock-Gradient: hsl(349, 71%, 52%) to hsl(349, 70%, 56%);
  --Dark-Text: hsl(229, 25%, 31%);
  --Header-Outline: hsl(217, 16%, 45%);
  --Score-Text: hsl(229, 64%, 46%);
  --background: hsl(214, 47%, 23%) to hsl(237, 49%, 15%);
}

#gameContainer {
  width: 90vw;
  height: 60vh;
  margin: 0 50%;
  transform: translate(-50%);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#gameTriangle {
  width: 100vw;
  height: 60vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../assets/bg-triangle.svg");
  background-repeat: no-repeat;
  background-position: center;
  z-index: -1;
}

.gameIcons {
  min-width: 8vw;
  min-height: 8vw;
  z-index: 99;
}

#gamePaperScissorsContainer {
  display: flex;
  width: 100vw;
  height: 35vh;
  display: flex;
  justify-content: center;
  gap: 200px;
}

#gameRockContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.gameIconsCircle {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: inset 0 10px 8px 2px rgba(0, 0, 0, 0.5);
  rotate: 180deg;
}

#rock {
  background: linear-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
}

#paper {
  background: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
}

#scissors {
  background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}

.gameIcons {
  width: 80px;
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: white;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: inset 0px 10px 8px 2px rgba(0, 0, 0, 0.6);
  rotate: 180deg;
}

#gameRock {
  background-image: url("../assets/icon-rock.svg");
}

#gameScissors {
  background-image: url("../assets/icon-scissors.svg");
}

#gamePaper {
  background-image: url("../assets/icon-paper.svg");
}

#resultContainer {
  width: 100vw;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 10vh;
}

#resultTopContainer {
  width: 100vw;
  display: flex;
  justify-content: center;
  gap: 20vw;
  align-items: center;
}

.resultGameIconsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.resultChoicesP {
  font-size: 1.5rem;
  margin-top: 2vh;
}

#resultTextContainer {
  display: flex;
  flex-direction: column;
  gap: 2vh;
  align-items: center;
}

#resultWinLose {
  font-size: 3rem;
  font-weight: 700;
}

#playAgain {
  padding: 2vh 2vw;
  background-color: white;
  color: hsl(229, 25%, 31%);
  text-align: center;
  border-radius: 5px;
  font-size: 2rem;
  margin-bottom: 5vh;
  cursor: pointer;
  transition: 0.3s;
}

#playAgain:hover {
  color: white;
  background-color: hsl(229, 25%, 31%);
}

#rpgameScissors {
  display: none;
}

#rpgameRock {
  display: none;
}

#rpgamePaper {
  display: none;
}

#rcgameScissors {
  display: none;
}

#rcgameRock {
  display: none;
}

#rcgamePaper {
  display: none;
}

@media (min-width: 1800px) {
  .gameIconsCircle {
    width: 1800px;
    height: 1800px;
  }

  .gameIcons {
    width: 100px;
    height: 100px;
  }
}

@media (min-width: 1200px) {
  .gameIconsCircle {
    width: 160px;
    height: 160px;
  }

  .gameIcons {
    width: 120px;
    height: 120px;
  }
}

@media (max-width: 400px) {
  .gameIconsCircle {
    width: 60px;
    height: 60px;
  }

  .gameIcons {
    width: 45px;
    height: 45px;
    background-size: 50%;
  }

  #gamePaperScissorsContainer {
    height: 160px;
    gap: 120px;
  }

  #gameTriangle {
    background-size: 200px;
  }

  .resultChoicesP {
    font-size: 1.2rem;
  }
}
