#rulesContainer {
  margin-top: 3vh;
  width: 100vw;
  display: flex;
  justify-content: center;
}

#rulesPContainer {
  padding: 2vh 2vw;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer;
}

#rulesPContainer:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

#rules {
  display: none;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 999;
  background-image: url("../assets/image-rules.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 40%;
  cursor: pointer;
}
